export const loader = async ({
  params,
  request
}) => {
  const project = await db.project.findUnique({
    where: {
      id: params.projectId
    },
    select: {
      User: true,
      config: true,
      id: true,
      type: true
    }
  });
  if (!project) throw json({
    message: `Formmy con id: ${params.projectId}, no encontrado`
  }, {
    status: 404
  });
  const isPro = project.User.plan === "PRO";
  delete project.User;
  return {
    isPro,
    project
  };
};
